import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Form, Button, Row, Col } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Message from "../../components/Message/Message";
import Loader from "../../components/Loader/Loader";
import { register } from "../../actions/userActions";
import FormContainer from "../../components/FormContainer/FormContainer";
import registerSchema from "../../validation/register.validation";
import validate from "../../validation/validation";
import { toast } from "react-hot-toast";
import PasswordRequirements from "../../components/PasswordRequirements/PasswordRequirements";
import "./registerPage.css";

const RegisterPage = ({ location, history }) => {
  window.scrollTo(0, 0);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState(null);

  const [pwdRequest, setPwdRequest] = useState(true);
  const [checks, setChecks] = useState({
    capsLetterCheck: false,
    numberCheck: false,
    pwdLengthCheck: false,
    specialCharCheck: false,
  });

  const dispatch = useDispatch();

  const userRegister = useSelector((state) => state.userRegister);
  const { loading, error, userInfo } = userRegister;

  const redirect = location.search ? location.search.split("=")[1] : "/";

  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  const submitHandler = (e) => {
    e.preventDefault();

    const error = validate({ name, email, password }, registerSchema);
    if (error.error) {
      let errorMsgs = "";
      for (let errorItem of error.error.details) {
        switch (errorItem.type) {
          case "string.min":
            if (errorItem.context.label === "Name")
              errorMsgs += `    2 `;
            if (errorItem.context.label === "Password")
              errorMsgs += `    8 `;
            if (errorItem.context.label === "Email")
              errorMsgs += `    6 `;
            break;
          case "string.max":
            if (errorItem.context.label === "Name")
              errorMsgs += `   50    `;
            if (errorItem.context.label === "Password")
              errorMsgs += `   20    `;
            if (errorItem.context.label === "Email")
              errorMsgs += `   100    `;
            break;
          default:
            errorMsgs += "    ";
            break;
        }
        toast.error(errorMsgs);
        return;
      }
    }
    if (password !== confirmPassword) {
      toast.error("  ");
    } else {
      dispatch(register(name, email, password));
    }
  };

  const handlePasswordStr = (e) => {
    setPassword(e.target.value);
  };

  const handleOnFocus = () => {
    setPwdRequest(true);
  };

  const handleOnBlur = () => {
    setPwdRequest(false);
  };

  const handleOnKeyUp = (e) => {
    if (e.key !== "Shift") {
      const { value } = e.target;
      const capsLetterCheck = /[A-Z]/.test(value);
      const numberCheck = /(?:\D*\d){4,}/.test(value);
      const pwdLengthCheck = value.length > 8;
      const specialCharCheck = /[!@#$%^&*_-]/.test(value);
      setChecks({
        capsLetterCheck,
        numberCheck,
        pwdLengthCheck,
        specialCharCheck,
      });
    }
  };

  const emailValidation = (e) => {
    setEmail(e.target.value);
    const regExp = /^[\w-/.]+@([\w-]+\.)+[\w-]{2,4}$/;
    if (regExp.test(email)) {
      setMessage("*");
    } else if (email === "") {
      setMessage("*  ");
    } else if (!regExp.test(email)) {
      setMessage("*  ");
    } else {
      setMessage("");
    }
  };

  return (
    <FormContainer>
      <h1 className="text-center"></h1>
      {/* {message && <Message variant="danger">{message}</Message>} */}
      {error && <Message variant="danger">{error}</Message>}
      {loading && <Loader />}
      <Form onSubmit={submitHandler}>
        <Form.Group controlId="name">
          <Form.Label className="text-center"></Form.Label>
          <Form.Control
            type="name"
            placeholder=" "
            value={name}
            onChange={(e) => setName(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="email">
          <Form.Label></Form.Label>
          <Form.Control
            type="email"
            placeholder="example@example.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            onInput={emailValidation}
          ></Form.Control>
        </Form.Group>

        <p className="email-validation mt-2">
          &nbsp;
          {message}
        </p>

        <Form.Group controlId="password">
          <Form.Label></Form.Label>
          <Form.Control
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            onInput={handlePasswordStr}
            onFocus={handleOnFocus}
            onBlur={handleOnBlur}
            onKeyUp={handleOnKeyUp}
          ></Form.Control>
        </Form.Group>

        {pwdRequest ? (
          <PasswordRequirements
            capsLetterFlag={checks.capsLetterCheck ? "valid" : "invalid"}
            numberFlag={checks.numberCheck ? "valid" : "invalid"}
            pwdLengthFlag={checks.pwdLengthCheck ? "valid" : "invalid"}
            specialCharFlag={checks.specialCharCheck ? "valid" : "invalid"}
          />
        ) : null}

        <Form.Group controlId="confirmPassword">
          <Form.Label> </Form.Label>
          <Form.Control
            type="password"
            placeholder=" "
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Button
          type="submit"
          variant="primary"
          className="mt-4 w-100 text-center"
        >
          
        </Button>
      </Form>

      <Row className="py-3">
        <Col className="text-center">
           ? &nbsp;
          <Link to={redirect ? `/login?redirect=${redirect}` : `/login`}>
             
          </Link>
        </Col>
      </Row>
    </FormContainer>
  );
};

export default RegisterPage;
